export default {
	data() {
		return {
			categories: [],
			currentIndex: 0,
			rightMenuData: [],
			scrollTop: 0
		}
	},
	created() {
		uni.getStorage({
			key: 'cates',
			fail() {
				this.getCategories()
			}
		});
	},
	methods: {
		getCategories() {
				this.$request('categories', {}).then(res => {
						this.categories = res.message
						this.rightMenuData = res.message[0]
						uni.setStorage({
							key: 'cates',
							data: res.message,
						});
				})
		},
		menuClick(index) {
			this.currentIndex = index
			this.rightMenuData = this.categories[index]
			this.scrollTop = 0
		},
		goSearch(){
			uni.navigateTo({
			    url: '/pages/seach/seach'
			});
		}
	},
	created() {

	},
	mounted() {
		this.getCategories()
	}

}
